<template>
  <div v-clickoutside="()=>visible=false" class="com-select" :class="{'is-disabeled' : disabled}">
    <div ref="reference" class="com-select-input" @click="clickSelectInput">
      <div class="one"></div>
      <!-- tags -->
      <div v-if="value.length>0" class="two">
        <!-- <template v-if="isCheckAll">
          <el-tag
            size="small"
            closable
            hit
            type="info"
            disable-transitions
            @close="cancelCheckAll()">
            <span class="el-select__tags-text"></span>
          </el-tag>
        </template>
        <template v-else> -->
        <el-tag
          v-for="item in selectedArr"
          :key="item[listKey]"
          size="small"
          closable
          hit
          type="info"
          disable-transitions
          @close="deleteTag(item)">
          <span class="el-select__tags-text">{{ item[listVal] }}</span>
        </el-tag>
        <!-- </template> -->
      </div>
      <div v-else class="two" style="color: #c4c9db;"></div>
      <div
        class="three"
        @mouseenter.stop="inputHovering = true"
        @mouseleave.stop="inputHovering = false"
      >
        <i v-if="showClose" class="el-select__caret el-input__icon el-icon-circle-close" @click.stop="cancelCheckAll"></i>
        <i v-else :class="`el-icon-arrow-${visible ? 'up' : 'down'} el-select__caret el-input__icon`"></i>
      </div>
    </div>
    <transition name="el-zoom-in-top">
      <!--  -->
      <the-select-menu
        v-show="visible"
        ref="popper"
        append-to-body>
        <!--  -->
        <el-input v-model="queryVal" placeholder="" size="small" clearable @input="handleSearch" @keyup.enter.native="handleSearch">
          <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-input>
        <!-- select -->
        <div class="com-select-body">
          <div
            v-for="(item, i) in list"
            v-show="isShowFilterArr ? item._isShow : item.isShow"
            :key="item[listKey]"
            :title="item[listVal]"
            :style="formatColumn()"
            :class="['com-option', {'selected': item.selected,}]"
            @click="selectOptionClick(item, i)"
          >
            {{ item[listVal] }}
          </div>
          <div v-if="list.length === 0" class="com-select-nodata">
            <img :src="require('@packages/assets/image/nodatatable.png')" />
            <div></div>
          </div>
          <template v-if="nextPageStaus">
            <i v-for="v in 3" :key="'AA' + v" :style="formatColumn()"></i>
          </template>
        </div>
        <!--  -->
        <div class="com-footer-page">
          <el-button size="small" :disabled="prePageStaus" @click="prePage"></el-button>
          <el-button type="primary" size="small" @click="checkAll"></el-button>
          <el-button size="small" :disabled="nextPageStaus" @click="nextPage"></el-button>
        </div>
      </the-select-menu>
    </transition>
  </div>
</template>
 
<script>
import TheSelectMenu from './select-dropdown.vue';
import Clickoutside from 'element-ui/src/utils/clickoutside';
import { valueEquals } from 'element-ui/src/utils/util';
export default {
  name: 'SelectPro',
  components: { TheSelectMenu },
  directives: { Clickoutside },
  props: {
    value: { // 
      type: Array,
      default: () => [],
      required: true
    },
    options: { // 
      type: Array,
      default: () => []
    },
    props: {
      type: Object,
      default: () => ({ label: 'label', value: 'value' })
    },
    column: { //  1-4
      type: Number,
      default: 3
    },
    disabled: { // 
      type: Boolean,
      default: false
    },
    selectAll: { // 
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      optionArr: this.initOptions(),
      inputHovering: false,
      selectedArr: [],
      filterArr: [],
      queryVal: '',
      visible: false,
      pageSize: this.column * 4,
      currentPage: 1,
      filterCurrentPage: 1
    };
  },
  computed: {
    list() {
      return this.isShowFilterArr ? this.filterArr : this.optionArr;
    },
    listKey() {
      return this.props['value'] || 'value';
    },
    listVal() {
      return this.props['label'] || 'label';
    },
    isCheckAll() {
      return this.selectedArr.length === this.optionArr.length;
    },
    isShowFilterArr() {
      return this.filterArr.length > 0 || this.queryVal !== '';
    },
    showClose() {
      return this.selectedArr.length > 0 && this.inputHovering && !this.disabled;
    },
    prePageStaus() {
      if (this.isShowFilterArr ? this.filterCurrentPage === 1 : this.currentPage === 1) {
        return true;
      }
      return false;
    },
    nextPageStaus() {
      const len = Math.ceil(this.list.length / this.pageSize);
      if (len === 0) return true;
      if (this.isShowFilterArr ? this.filterCurrentPage === len : this.currentPage === len) {
        return true;
      }
      return false;
    }
  },
  watch: {
    selectedArr(val) {
      const arr = val.map(item => {
        return item[this.listKey];
      });
      this.$emit('input', arr);
    },
    value(val, oldVal) {
      if (!valueEquals(val, oldVal)) {
        this.selectedArr = [];
        this.optionArr.forEach(item => {
          if (this.selectAll || val.includes(item[this.listKey])) {
            item.selected = true;
            this.selectedArr.push(item);
          } else {
            item.selected = false;
          }
        });
      }
    },
    options() {
      this.optionArr = this.initOptions();
      // 
      this.selectedArr = [];
      this.optionArr.forEach(item => {
        if (this.selectAll || this.value.includes(item[this.listKey])) {
          this.selectedArr.push(item);
        }
      });
      // 
      this.currentPage = 1;
      this.filterCurrentPage = 1;
    }
  },
  mounted() {
    // 
    this.optionArr.forEach(item => {
      if (this.selectAll || this.value.includes(item[this.listKey])) {
        this.selectedArr.push(item);
      }
    });
  },
  methods: {
    initOptions() { // 
      return this.options.map((item, i) => {
        return {
          isShow: i < this.column * 4,
          _isShow: false,
          selected: this.selectAll || this.value.includes(item[this.props['value'] || 'value']),
          ...item
        };
      });
    },
    deleteTag(item) { // tag
      if (this.disabled) return;
      this.selectedArr.some((v, i) => {
        if (v[this.listKey] === item[this.listKey]) {
          item.selected = false;
          this.selectedArr.splice(i, 1);
          return true;
        }
      });
    },
    handleSearch() { // 
      if (this.queryVal === '') { // 
        this.filterArr = [];
        return;
      }
      console.log(this.queryVal);
      // 
      this.filterArr = [];
      // 
      this.filterCurrentPage = 1;
      let num = 0;
      this.optionArr.forEach(item => {
        if (item[this.listVal].indexOf(this.queryVal) !== -1) { // like 
          item._isShow = num < this.pageSize; // 
          this.filterArr.push(item);
          num++;
        }
      });
    },
    clickSelectInput() { // 
      if (this.disabled) return;
      this.visible = !this.visible;
    },
    selectOptionClick(item, index) { // check
      item.selected = !item.selected;
      // 
      if (this.selectedArr.some(v => v[this.listKey] === item[this.listKey])) {
        this.deleteTag(item);
      } else {
        this.selectedArr.push(item);
      }
    },
    checkAll() { // 
      this.selectedArr = [];
      this.optionArr.forEach(item => {
        item.selected = true;
        this.selectedArr.push(item);
      });
    },
    cancelCheckAll() { // 
      if (this.disabled) return;
      this.optionArr.forEach(item => {
        item.selected = false;
      });
      this.selectedArr = [];
    },
    prePage() { // 
      if (this.isShowFilterArr) { // 
        this.changeFilterPageShow(false);
        this.filterCurrentPage--;
        this.changeFilterPageShow(true);
      } else {
        this.changePageShow(false);
        this.currentPage--;
        this.changePageShow(true);
      }
    },
    nextPage() { // 
      if (this.isShowFilterArr) { // 
        this.changeFilterPageShow(false);
        this.filterCurrentPage++;
        this.changeFilterPageShow(true);
      } else {
        this.changePageShow(false);
        this.currentPage++;
        this.changePageShow(true);
      }
    },
    changePageShow(isOrNo) { // 
      const num = (this.currentPage - 1) * this.pageSize;
      const num2 = num + this.pageSize;
      for (let index = num; index < num2; index++) {
        if (this.optionArr[index]) this.optionArr[index].isShow = isOrNo;
      }
    },
    changeFilterPageShow(isOrNo) { // 
      const num = (this.filterCurrentPage - 1) * this.pageSize;
      const num2 = num + this.pageSize;
      for (let index = num; index < num2; index++) {
        if (this.filterArr[index]) this.filterArr[index]._isShow = isOrNo;
      }
    },
    formatColumn() {
      // 
      if (this.column === 1) {
        return { width: '100%' };
      } else if (this.column === 2) {
        return { width: '49%' };
      } else if (this.column === 3) {
        return { width: '32%' };
      } else {
        return { width: '24%' };
      }
    }
  }
};
</script>
<style lang="less" scoped>
.com-select {
  border-radius: 4px;
  border: 1px solid #DCDFE6;
  box-sizing: border-box;
  cursor: pointer;
  background: #ffffff;
  min-width: 230px;
  min-height: 32px;
  .com-select-input {
    display: flex;
    align-items: center;
    .one {
      width: 10px;
    }
    .two {
      flex: 1;
      line-height: 29px;
      padding: 0 0 1px 0;
      /deep/ .el-tag {
        background-color: #f4f4f5;
        border-color: #e9e9eb;
        color: #909399;
        margin-right: 6px;
      }
      /deep/ .el-tag__close.el-icon-close {
        background-color: #C0C4CC;
      }
    }
    .three {
      width: 30px;
    }
  }
}
.is-disabeled {
  cursor: not-allowed;
  background-color: #F5F7FA;
  border-color: #E4E7ED;
}
.el-select-dropdown {
  height: 280px;
  padding: 10px;
  border: 1px solid #E4E7ED;
  border-radius: 4px;
  box-shadow: 1px 2px 6px 0px #7d7d7d;
  box-sizing: border-box;
  cursor: default;
  .com-select-body {
    margin-top: 6px;
    height: 178px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: flex-start;
    .com-option {
      // width: 32%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #797D82;
      // background-color: rgb(240, 248, 250);
      border: 1px solid #dcdfe6;
      height: 32px;
      line-height: 32px;
      box-sizing: border-box;
      position: relative;
      text-align: center;
      margin-bottom: 10px;
      cursor: pointer;
      &:hover {
        border-color: #409EFF;
        color: #409EFF;
      }
    }
    // 
    .selected {
      border-color: #409EFF;
      &::after{
        content: '';
        width: 19px;
        height: 19px;
        background: url("~@packages/assets/image/check.png") no-repeat 0 0;
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
    .com-select-nodata {
      color: #C0C4CC;
      width: 100%;
      text-align: center;
    }
  }
  // 
  .com-footer-page {
    display: flex;
    justify-content: space-between;
  }
}
</style>